// cta banner
.cta-banner {
  padding: 1rem 0;
  background: $Soft-Blue;
  color: white;
  @include section-heading;
  &__headline {
    color: white;
  }
  &__supporting-copy {
    color: white;
  }
  @include mQ_desktop {
    .cta-holder {
      justify-content: center;
    }
  }
  &__headline--team {
    font-weight: 400;
  }
}
